import { Injectable } from '@angular/core';
import { Observable, of } from 'rxjs';
import { HttpClient } from '@angular/common/http';
import { environment } from '../../../environments/environment';

interface LedgerTransaction {
  accountID: string;
  currency: string;
  amount: number;
  from: string;
  provider: string;
  initialValue: string;
}

interface LedgerAccount {
  accountID: string;
  currency: string;
  note: string;
  folio: string;
}

@Injectable({
  providedIn: 'root'
})
export class LedgerService {

  constructor(
    private http: HttpClient
  ) { }

  private headers({ apiKey }) {
    return {
      headers: {
        'apicode': apiKey
      }
    };
  }

  createAccount(account: LedgerAccount, apiKey): Observable<{}> {
    return this.http.post(`${environment.ledgerUrl}create`, { ...account }, this.headers({ apiKey }));
  }

  createTx(tx: LedgerTransaction, apiKey: string): Observable<{}> {
    return this.http.post(`${environment.ledgerUrl}tx`, { ...tx, operation: '+' }, this.headers({ apiKey }));
  }
}
